////        if (this.tags[i].pn!==null) this.tags[i].pn.style.display = (this.pn_filter_rexp===null || this.pn_filter_rexp.test(i))? '' : 'none';
      if (!init) this.update_pn(pref_obj === pref.catalog.filter.tag_search);
