      var tag_ci = tag.toLowerCase();
      for (var j=0;j<pns.length;j++) {
        var txt = pns[j].textContent;
        if (txt===tag || (pref.liveTag.ci && txt.toLowerCase()===tag_ci)) {
          this.color_tag_node(pns[j],tag);
          break;
        }
      }
