      function catalog_filter_query(name, no_reset){
        var tgt_th = threads[name];
        var val_old = tgt_th[9][0];
        tgt_th[9][0] = catalog_filter_query_1(name, tgt_th);
        if (!no_reset && val_old !== tgt_th[9][0]) drawn_idx = 0;
        return tgt_th[9];
      }
